    hotelName: "Еко-садиба «Васкул»",
    hotelUrl: "./hotel.html",
    hotelAddress: "с. Лумшори, вул. Лугова, 5, Закарпатська область, Україна",
    hotelPhone: "+380969036281",
    hotelCoinsIconSrc: "public/images/main-cards/icon-coins.svg",
